




























import { Vue, Component } from "vue-property-decorator";
import Ava from "@/assets/images/svg-components/ava.vue";

// array with texts to type in typewriter
const dataText = [
  "Problem solver",
  "Freelancer",
  "Solid gamer",
  "Dead serious guy",
];

@Component({
  components: {
    Ava,
  },
})
class Intro extends Vue {
  isLastOfAnimation = false;
  typingText = "";

  onLastOfAnimation() {
    this.isLastOfAnimation = true;
    // start the text animation
    setTimeout(() => this.startTextAnimation(0), 2000);
  }

  // start a typewriter animation for a text in the dataText array
  startTextAnimation(i: number) {
    if (typeof dataText[i] == "undefined") {
      setTimeout(() => {
        this.startTextAnimation(0);
      }, 7000);
    }
    // check if dataText[i] exists
    if (i < dataText[i].length) {
      // text exists! start typewriter animation
      this.typeWriter(dataText[i], 0, () => {
        // after callback (and whole text has been animated), start next text
        this.startTextAnimation(i + 1);
      });
    }
  }

  get boundTypeWriter() {
    return this.typeWriter.bind(this);
  }

  // type one text in the typwriter
  // keeps calling itself until the text is finished
  typeWriter(text: string, i: number, fnCallback: TimerHandler) {
    // chekc if text isn't finished yet
    if (i < text.length) {
      // add next character to h1
      this.typingText =
        text.substring(0, i + 1) + '<span aria-hidden="true"></span>';

      // wait for a while and call this function again for next character
      setTimeout(() => {
        this.boundTypeWriter(text, i + 1, fnCallback);
      }, 75);
    }
    // text finished, call callback if there is a callback function
    else if (typeof fnCallback == "function") {
      // call callback after timeout
      setTimeout(fnCallback, 1700);
    }
  }
}
export default Intro;
